import axios from 'axios';

export default {
  data() {
    return {
      empresas: []
    };
  },
  methods: {
    fetchEmpresas() {
      axios.get('http://localhost:8000/api/empresa/', {
        headers: {
          'X-CSRFToken': this.$cookies.get('csrftoken')
        }
      })
      .then(response => {
        this.empresas = response.data;
      })
      .catch(error => {
        console.error("There was an error fetching the items:", error);
      });
    }
  }
};