// src/router.js
import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  mode: 'history', // Use 'history' mode for cleaner URLs without hash (#)
  routes: [
    {
      path: '/informacoesGerais',
      name: 'informacoesGerais',
      component: () => import('./components/informacoesGerais.vue') // Example: Import your Home component
    },
  ]
});